export const Header = ( ) => {

  const [sidebarOpen, setSidebarOpen] = useState( false )

  return (
    <div>
      <Transition.Root show={sidebarOpen} as={Fragment}>
          <Dialog
            as="div"
            static
            className="fixed inset-0 flex z-40 md:hidden"
            open={sidebarOpen}
            onClose={setSidebarOpen}
          >
            <Transition.Child
              as={Fragment}
              enter="transition-opacity ease-linear duration-300"
              enterFrom="opacity-0"
              enterTo="opacity-100"
              leave="transition-opacity ease-linear duration-300"
              leaveFrom="opacity-100"
              leaveTo="opacity-0"
            >
              <Dialog.Overlay className="fixed inset-0 bg-gray-600 bg-opacity-75" />
            </Transition.Child>
            <Transition.Child
              as={Fragment}
              enter="transition ease-in-out duration-300 transform"
              enterFrom="-translate-x-full"
              enterTo="translate-x-0"
              leave="transition ease-in-out duration-300 transform"
              leaveFrom="translate-x-0"
              leaveTo="-translate-x-full"
            >
              <div className="relative flex-1 flex flex-col max-w-xs w-full pb-4 bg-gray-800">
                <Transition.Child
                  as={Fragment}
                  enter="ease-in-out duration-300"
                  enterFrom="opacity-0"
                  enterTo="opacity-100"
                  leave="ease-in-out duration-300"
                  leaveFrom="opacity-100"
                  leaveTo="opacity-0"
                >
                  <div className="absolute top-0 right-0 -mr-12 pt-2">
                    <button
                      className="ml-1 flex items-center justify-center h-10 w-10 rounded-full focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white"
                      onClick={() => setSidebarOpen(false)}
                    >
                      <span className="sr-only">Close sidebar</span>
                      <XIcon className="h-6 w-6 text-white" aria-hidden="true" />
                    </button>
                  </div>
                </Transition.Child>
                <div className="flex-shrink-0 flex content-center px-4 h-40 bg-gray-800">
                  {/* <img
                    className="h-8 w-auto"
                    src="../../assets/images/download.png"
                    alt="Workflow"
                  /> */}
                {/* <h1 className="text-gray-300 text-3xl">React Concepts</h1> */}
                <a rel="home" href="/">
                 <div className="grid grid-flow-row auto-rows-max ">
                    <div className="content-center px-24 py-2"><img className="" src="https://jacekjeznach.com/wp-content/themes/new/img/logo.png" alt="Jamal" /></div>
                  <div><h1 className=" text-gray-300 px-20 text-3xl">Jamal</h1></div>
                  <div><span className="text-sm text-gray-300 px-16 content-center">Full Stack Developer</span></div>
                  </div>
                </a>
                
                </div>
                <div className="mt-5 flex-1 h-0 overflow-y-auto">
                  <nav className="px-2 space-y-1">
                    {navigation.map((item) => (
                      <a
                        key={item.name}
                        href={item.href}
                        className={classNames(
                          item.current ? 'bg-gray-900 text-gray-400' : 'text-gray-400 hover:bg-gray-700 hover:text-white',
                          'group flex items-center px-2 hover:text-green-300 py-2 text-base font-medium rounded-md'
                        )}
                      >
                        <item.icon
                          key={item.name}
                          className={classNames(
                            item.current ? 'text-gray-400' : 'text-gray-400 group-hover:text-green-300',
                            'mr-4 flex-shrink-0 h-6 w-6'
                          )}
                          aria-hidden="true"
                        />
                        {item.name}
                      </a>
                    ))}
                  </nav>
              </div>
              <div className="bg-gray-800 pl-4 pb-5 space-y-2 xl:col-span-1">
              <div className="flex space-x-2">
                {footerNavigation.social.map((item) => (
                  <a key={item.name} target="_blank" href={item.href} className="text-gray-400 hover:text-gray-300">
                    <span className="sr-only">{item.name}</span>
                    <item.icon className="h-6 w-6" aria-hidden="true" />
                  </a>
                ))}
              </div>
      </div>
              </div>
            </Transition.Child>
            <div className="flex-shrink-0 w-14" aria-hidden="true">
              {/* Dummy element to force sidebar to shrink to fit close icon */}
            </div>
          </Dialog>
        </Transition.Root>
      <button
        className="float-left px-6 py-6 border-gray-200 text-gray-500 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500 md:hidden"
        onClick={() => setSidebarOpen(true)}
      >
        <span className="sr-only">Open sidebar</span>
        <MenuAlt2Icon className="h-6 w-6" aria-hidden="true" />
      </button>
      
    </div>
  )
}



